@media (min-width: 680px) {
  .bigModal {
    top: 0 !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    width: 100% !important;
  }

  .platform-ios.platform-cordova .modal-wrapper .modal {

    .bar-header:not(.bar-subheader) > * {
      margin-top: 20px;
    }

    .bar-header:not(.bar-subheader) {
      height: 64px;
    }

    .has-header, .bar-subheader {
      top: 64px;
    }
  }
}

.center-content {
  display: table !important;
  width: 100% !important;
  height: 100% !important;

  .scroll {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
}

.animate-item {
  animation: fadeInLeft 1s;
}
